<template>
  <div>
    <!-- 头部 -->
    <nav-bar class="home-nav">
      <div slot="left" class="left" @click="$router.go(-1)">
        <img src="~assets/img/common/back.svg" />
      </div>
      <div slot="center" class="header">
        <span>选择地址</span>
      </div>
    </nav-bar>

    <van-address-list
      class="address"
      v-model="chosenAddressId"
      :list="list"
      :disabled-list="disabledList"
      default-tag-text="默认"
      @add="handleAdd"
      @select="handleSelect"
      @edit="handleEdit"
    />
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar";
import {mapMutations } from "vuex";
export default {
  name: "ChooseAddress",
  data() {
    return {
      chosenAddressId: "1",
      list: [],
      disabledList: []
    };
  },
  components: {
    NavBar
  },
  created() {},
  mounted() {
    this.fetchAddressList();
    this.chosenAddressId = this.$route.query.id*1;
  },
  methods: {
    ...mapMutations(["chooseAddress"]),
    handleEdit(item){
      let id =item.id;
      this.$router.push({name:"editaddress",query:{id:id}})
    },
    handleSelect(item){
      console.log(item);
      this.chooseAddress(item.id);
        setTimeout(()=>{
          this.$router.go(-1);
        },500)
    },
    handleAdd(){
      this.$router.push({name:"address"})
    },
    async fetchAddressList() {
      let res = await this.$http.get(this.$api.ADDRESS, {
        uid: localStorage.uid
      });
      console.log(res);
      this.list = res.data.data.map(v => ({
        id: v.id,
        name: v.connect_name,
        tel: v.connect_phone,
        address: v.address,
        isDefault: v.default
      }));
    }
  }
};
</script>
<style lang='scss' scoped>
.nav-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  background-color: #fff;
}
.left {
  padding-top: 5px;
  padding-left: 5px;
}
.address {
  margin-top: 1.9rem;
}
</style>
